import React, { useState, useEffect, useRef } from "react";
import { DragDropContext } from "@hello-pangea/dnd";
import Column from "./Column";

const initialData = {
  tasks: {
    "task-1": { id: "task-1", title: "Fix Bug", description: "Resolve login page crash", priority: "Low", createdAt: new Date().toLocaleString() },
    "task-2": { id: "task-2", title: "Deploy Website", description: "Push updates to production", priority: "High", createdAt: new Date().toLocaleString() },
    "task-3": { id: "task-3", title: "Code Review", description: "Review PR #24", priority: "Medium", createdAt: new Date().toLocaleString() },
    "task-4": { id: "task-4", title: "Team Meeting", description: "Weekly sync with devs", priority: "High", createdAt: new Date().toLocaleString() }
  },
  columns: {
    "column-1": { id: "column-1", title: "To Do", taskIds: ["task-1", "task-2", "task-3", "task-4"] },
    "column-2": { id: "column-2", title: "In Progress", taskIds: [] },
    "column-3": { id: "column-3", title: "Done", taskIds: [] }
  },
  columnOrder: ["column-1", "column-2", "column-3"]
};

const STORAGE_KEY = "kanbanState";

function Board() {

  const [state, setState] = useState(() => {
    try {
      const raw = localStorage.getItem(STORAGE_KEY);
      return raw ? JSON.parse(raw) : initialData;
    } catch (e) {
      console.warn("Failed to parse saved state, using initialData.", e);
      return initialData;
    }
  });

  const [darkMode, setDarkMode] = useState(false);

 
  const saveTimer = useRef(null);
  useEffect(() => {
    
    if (saveTimer.current) clearTimeout(saveTimer.current);
    saveTimer.current = setTimeout(() => {
      try {
        localStorage.setItem(STORAGE_KEY, JSON.stringify(state));
      } catch (e) {
        console.error("Failed to save state to localStorage", e);
      }
    }, 300);

    return () => {
      if (saveTimer.current) clearTimeout(saveTimer.current);
    };
  }, [state]);

  
  const resetBoard = () => {
    localStorage.removeItem(STORAGE_KEY);
    setState(initialData);
  };

  const onDragEnd = (result) => {
    const { destination, source, draggableId } = result;
    if (!destination) return;
    if (destination.droppableId === source.droppableId && destination.index === source.index) return;

    const start = state.columns[source.droppableId];
    const finish = state.columns[destination.droppableId];

    if (start === finish) {
      const newTaskIds = Array.from(start.taskIds);
      newTaskIds.splice(source.index, 1);
      newTaskIds.splice(destination.index, 0, draggableId);
      const newColumn = { ...start, taskIds: newTaskIds };
      setState((prev) => ({ ...prev, columns: { ...prev.columns, [newColumn.id]: newColumn } }));
      return;
    }

    const startTaskIds = Array.from(start.taskIds);
    startTaskIds.splice(source.index, 1);
    const newStart = { ...start, taskIds: startTaskIds };

    const finishTaskIds = Array.from(finish.taskIds);
    finishTaskIds.splice(destination.index, 0, draggableId);
    const newFinish = { ...finish, taskIds: finishTaskIds };

    setState((prev) => ({
      ...prev,
      columns: {
        ...prev.columns,
        [newStart.id]: newStart,
        [newFinish.id]: newFinish
      }
    }));
  };

  const addTask = (columnId, title, description, priority) => {
    const newTaskId = `task-${Date.now()}`;
    const newTask = { id: newTaskId, title, description, priority, createdAt: new Date().toLocaleString() };

    setState((prev) => ({
      ...prev,
      tasks: { ...prev.tasks, [newTaskId]: newTask },
      columns: {
        ...prev.columns,
        [columnId]: {
          ...prev.columns[columnId],
          taskIds: [...prev.columns[columnId].taskIds, newTaskId]
        }
      }
    }));
  };

  const editTask = (taskId, title, description, priority) => {
    setState((prev) => ({
      ...prev,
      tasks: { ...prev.tasks, [taskId]: { ...prev.tasks[taskId], title, description, priority } }
    }));
  };

  const deleteTask = (columnId, taskId) => {
    setState((prev) => ({
      ...prev,
      tasks: Object.fromEntries(Object.entries(prev.tasks).filter(([id]) => id !== taskId)),
      columns: {
        ...prev.columns,
        [columnId]: { ...prev.columns[columnId], taskIds: prev.columns[columnId].taskIds.filter((id) => id !== taskId) }
      }
    }));
  };

  return (
    <div className={darkMode ? "bg-gray-900 text-white min-h-screen" : "bg-gradient-to-br from-indigo-100 via-purple-100 to-pink-100 min-h-screen"}>
      <div className="flex justify-between items-center px-6 py-4 border-b border-gray-300 dark:border-gray-700">
        <h1 className="text-4xl font-extrabold bg-gradient-to-r from-purple-500 to-yellow-500 bg-clip-text text-transparent">Kanban Board</h1>
        <div className="flex gap-3 items-center">
        <button onClick={resetBoard} className="px-4 py-2 rounded-lg bg-gradient-to-r from-green-500 to-indigo-500 hover:scale-105 transition-transform duration-300 text-white font-semibold">Reset</button>
          <button onClick={() => setDarkMode(!darkMode)} className="px-4 py-2 rounded-lg bg-gradient-to-r from-purple-500 to-indigo-500 hover:scale-105 transition-transform duration-300 text-white font-semibold">
            {darkMode ? "Light Mode" : "Dark Mode"}
          </button>
          
        </div>
      </div>

      <DragDropContext onDragEnd={onDragEnd}>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 p-6">
          {state?.columnOrder?.map((columnId) => {
            const column = state.columns?.[columnId];
            if (!column) return null;
            const tasks = column?.taskIds?.map((taskId) => state.tasks[taskId]) || [];
            return (
              <Column
                key={column.id}
                column={column}
                tasks={tasks}
                addTask={addTask}
                editTask={editTask}
                deleteTask={deleteTask}
              />
            );
          })}
        </div>
      </DragDropContext>
    </div>
  );
}

           
export default Board;
