import React, { useState } from "react";
import { Droppable, Draggable } from "@hello-pangea/dnd";

export default function Column({ column, tasks, addTask, editTask, deleteTask }) {
  const [showAddModal, setShowAddModal] = useState(false);
  const [newTitle, setNewTitle] = useState("");
  const [newDescription, setNewDescription] = useState("");
  const [newPriority, setNewPriority] = useState("Low");

  const [editingId, setEditingId] = useState(null);
  const [editTitle, setEditTitle] = useState("");
  const [editDescription, setEditDescription] = useState("");
  const [editPriority, setEditPriority] = useState("Low");

  const priorityColors = {
    Low: "bg-green-200 text-green-800",
    Medium: "bg-yellow-200 text-yellow-800",
    High: "bg-red-200 text-red-800",
  };

  const handleAddSave = (e) => {
    e.preventDefault();
    if (!newTitle.trim()) return;
    addTask(column.id, newTitle, newDescription, newPriority);
    setNewTitle("");
    setNewDescription("");
    setNewPriority("Low");
    setShowAddModal(false);
  };

  const handleEditSave = (taskId) => {
    if (!editTitle.trim()) return;
    editTask(taskId, editTitle, editDescription, editPriority);
    setEditingId(null);
  };

  return (
  <div className="bg-white rounded-2xl shadow-lg p-4 flex flex-col relative">
      <h2 className="text-xl font-bold mb-4 text-gray-700">{column.title}</h2>

      {/* Tasks List */}
      <Droppable droppableId={column.id}>
        {(provided) => (
          <div ref={provided.innerRef} {...provided.droppableProps} className="space-y-3 flex-1 min-h-[100px]">
            {tasks.map((task, index) => (
              <Draggable key={task.id} draggableId={task.id} index={index}>
                {(provided) => (
                  <div
                    ref={provided.innerRef}
                    {...provided.draggableProps}
                    {...provided.dragHandleProps}
                    className={` bg-gradient-to-r from-purple-300 to-blue-300 p-3 rounded shadow hover:shadow-lg transition cursor-pointer p-3 rounded shadow hover:shadow-lg transition border-l-4 ${priorityColors[task.priority]}`}
                  >
                    {editingId === task.id ? (
                      <>
                        <input
                          type="text"
                          value={editTitle}
                          onChange={(e) => setEditTitle(e.target.value)}
                          className="border rounded w-full p-1 mb-1"
                          placeholder="Title"
                        />
                        <textarea
                          value={editDescription}
                          onChange={(e) => setEditDescription(e.target.value)}
                          className="border rounded w-full p-1 mb-1"
                          placeholder="Description"
                        />
                        <select
                          value={editPriority}
                          onChange={(e) => setEditPriority(e.target.value)}
                          className="border rounded w-full p-1 mb-2"
                        >
                          <option>Low</option>
                          <option>Medium</option>
                          <option>High</option>
                        </select>
                        <button
                          onClick={() => handleEditSave(task.id)}
                          className="bg-green-500 text-white px-2 py-1 rounded mr-2 hover:brightness-110 transition"
                        >
                          Save
                        </button>
                        <button
                          onClick={() => setEditingId(null)}
                          className="bg-gray-500 text-white px-2 py-1 rounded hover:brightness-110 transition"
                        >
                          Cancel
                        </button>
                      </>
                    ) : (
                      <>
                        <h3 className="font-bold">{task.title}</h3>
                        <p className="text-sm">{task.description}</p>
                        <p className="text-xs font-semibold">Priority: {task.priority}</p>
                        <p className="text-xs text-gray-600">Created: {task.createdAt}</p>

                        <div className="mt-2 flex gap-2">
                          <button
                            onClick={() => {
                              setEditingId(task.id);
                              setEditTitle(task.title);
                              setEditDescription(task.description);
                              setEditPriority(task.priority);
                            }}
                            className="bg-blue-500 text-white px-2 py-1 rounded hover:scale-105 transition-transform"
                          >
                            Edit
                          </button>
                          <button
                            onClick={() => deleteTask(column.id, task.id)}
                            className="bg-red-500 text-white px-2 py-1 rounded hover:scale-105 transition-transform"
                          >
                            Delete
                          </button>
                        </div>
                      </>
                    )}
                  </div>
                )}
              </Draggable>
            ))}
            {provided.placeholder}
          </div>
        )}
      </Droppable>

      {/* ADD TASK BUTTON & MODAL: Only for "To Do" Column */}
      {column.title === "To Do" && (
        <>
          <button
            onClick={() => setShowAddModal(true)}
            className="mt-4 bg-gradient-to-r from-purple-400 to-blue-500 text-white px-4 py-2 rounded-lg hover:brightness-110 transition"
          >
            + Add Task
          </button>

          {showAddModal && (
            <>
              <div
                className="fixed inset-0 bg-black bg-opacity-50 z-40"
                onClick={() => setShowAddModal(false)}
              />
              <form
                onSubmit={handleAddSave}
                className="fixed z-50 top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 bg-white dark:bg-gray-800 rounded-lg shadow-lg p-6 w-80"
              >
                <h3 className="text-lg font-bold mb-4 text-gray-700 dark:text-gray-200">Add New Task</h3>
                <input
                  type="text"
                  value={newTitle}
                  onChange={(e) => setNewTitle(e.target.value)}
                  placeholder="Title"
                  className="border rounded w-full p-2 mb-3 focus:outline-none focus:ring-2 focus:ring-indigo-500"
                  required
                />
                <textarea
                  value={newDescription}
                  onChange={(e) => setNewDescription(e.target.value)}
                  placeholder="Description"
                  className="border rounded w-full p-2 mb-3 focus:outline-none focus:ring-2 focus:ring-indigo-500"
                />
                <select
                  value={newPriority}
                  onChange={(e) => setNewPriority(e.target.value)}
                  className="border rounded w-full p-2 mb-4 focus:outline-none focus:ring-2 focus:ring-indigo-500"
                >
                  <option>Low</option>
                  <option>Medium</option>
                  <option>High</option>
                </select>
                <div className="flex justify-end gap-3">
                  <button
                    type="submit"
                    className="bg-indigo-600 text-white px-4 py-2 rounded hover:bg-indigo-700 transition"
                  >
                    Add Task
                  </button>
                  <button
                    type="button"
                    onClick={() => setShowAddModal(false)}
                    className="bg-gray-400 text-white px-4 py-2 rounded hover:bg-gray-500 transition"
                  >
                    Cancel
                  </button>
                </div>
              </form>
            </>
          )}
        </>
      )}
    </div>
  );
}
